.ub-flex { display: -webkit-box; display: -ms-flexbox; display: flex }

@media (min-width: 40em) {
  .ub-sm-flex { display: -webkit-box; display: -ms-flexbox; display: flex }
}

@media (min-width: 52em) {
  .ub-md-flex { display: -webkit-box; display: -ms-flexbox; display: flex }
}

@media (min-width: 64em) {
  .ub-lg-flex { display: -webkit-box; display: -ms-flexbox; display: flex }
}

.ub-flex-column  { -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column }

.ub-flex-wrap    { -ms-flex-wrap: wrap; flex-wrap: wrap }

.ub-items-start    { -webkit-box-align: start; -ms-flex-align: start; align-items: flex-start }

.ub-items-end      { -webkit-box-align: end; -ms-flex-align: end; align-items: flex-end }

.ub-items-center   { -webkit-box-align: center; -ms-flex-align: center; align-items: center }

.ub-items-baseline { -webkit-box-align: baseline; -ms-flex-align: baseline; align-items: baseline }

.ub-items-stretch  { -webkit-box-align: stretch; -ms-flex-align: stretch; align-items: stretch }

.ub-self-start    { -ms-flex-item-align: start; align-self: flex-start }

.ub-self-end      { -ms-flex-item-align: end; align-self: flex-end }

.ub-self-center   { -ms-flex-item-align: center; align-self: center }

.ub-self-baseline { -ms-flex-item-align: baseline; align-self: baseline }

.ub-self-stretch  { -ms-flex-item-align: stretch; align-self: stretch }

.ub-justify-start   { -webkit-box-pack: start; -ms-flex-pack: start; justify-content: flex-start }

.ub-justify-end     { -webkit-box-pack: end; -ms-flex-pack: end; justify-content: flex-end }

.ub-justify-center  { -webkit-box-pack: center; -ms-flex-pack: center; justify-content: center }

.ub-justify-between { -webkit-box-pack: justify; -ms-flex-pack: justify; justify-content: space-between }

.ub-justify-around  { -ms-flex-pack: distribute; justify-content: space-around }

.ub-content-start   { -ms-flex-line-pack: start; align-content: flex-start }

.ub-content-end     { -ms-flex-line-pack: end; align-content: flex-end }

.ub-content-center  { -ms-flex-line-pack: center; align-content: center }

.ub-content-between { -ms-flex-line-pack: justify; align-content: space-between }

.ub-content-around  { -ms-flex-line-pack: distribute; align-content: space-around }

.ub-content-stretch { -ms-flex-line-pack: stretch; align-content: stretch }

/* 1. Fix for Chrome 44 bug. https://code.google.com/p/chromium/issues/detail?id=506893 */

.ub-flex-auto {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  min-width: 0; /* 1 */
  min-height: 0; /* 1 */
}

.ub-flex-none { -webkit-box-flex: 0; -ms-flex: none; flex: none }

.ub-order-0 { -webkit-box-ordinal-group: 1; -ms-flex-order: 0; order: 0 }

.ub-order-1 { -webkit-box-ordinal-group: 2; -ms-flex-order: 1; order: 1 }

.ub-order-2 { -webkit-box-ordinal-group: 3; -ms-flex-order: 2; order: 2 }

.ub-order-3 { -webkit-box-ordinal-group: 4; -ms-flex-order: 3; order: 3 }

.ub-order-last { -webkit-box-ordinal-group: 100000; -ms-flex-order: 99999; order: 99999 }
